@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800&display=swap);
body {
  margin: 0;
  padding:0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center;min-height:100vh;height:100%;color:#fff;background:radial-gradient(#2b3760, #0e1429)}

.nav{display:flex;justify-content:space-between;align-items:center}.nav div{display:flex;margin-right:5%}.nav select{width:78px;display:block;font-size:14px;font-family:sans-serif;font-weight:600;color:#444;line-height:1.3;padding:.2em 1.4em .2em .4em;box-sizing:border-box;margin:10px 5%;border:1px solid #aaa;box-shadow:0 1px 0 1px rgba(0,0,0,0.04);border-radius:.5em;outline:none}.nav ul{display:flex;list-style:none;margin:0}.nav li>*{display:block;padding:20px 20px;color:white;text-decoration:none;font-weight:500;cursor:pointer;transition:0.5s}.nav li>*:hover{transform:translateY(-10px)}@media (max-width: 450px){.nav{justify-content:center}.nav p{display:none}.nav select{width:50px;padding:0}.nav li>*{padding:20px 15px}}

.converter{display:flex;flex-direction:column;align-items:center;color:white;height:auto;padding:0 5% 5%}.converter__container{display:flex;flex-direction:column;grid-column-gap:20px;-webkit-column-gap:20px;column-gap:20px;width:50%;height:60%;box-shadow:0px 10px 10px 5px #0F1423;border-radius:8px;padding:20px}.converter__operations{display:grid;grid-template-columns:1fr 2fr}.converter__btn{display:flex;align-items:center;justify-content:space-evenly}.converter__btn button{display:block;width:50%;height:60px;font-weight:600;margin-top:16px;color:#fff;outline:none;background-color:#E56464;border:none;cursor:pointer;transition:0.5s;border-radius:1rem}.converter__btn button:hover{transform:scale(1.1)}.converter__inputBox{padding:0 20%;margin-bottom:35px}.converter__inputBox input{display:block;font-size:16px;font-weight:700;color:#444;line-height:1.3;margin-top:20px;padding:.6em 1.4em .5em .8em;width:100%;box-sizing:border-box;border:1px solid #aaa;box-shadow:0 1px 0 1px rgba(0,0,0,0.04);border-radius:8px}.converter__resultbox{margin-top:20px;font-size:.8em}.converter__resultbox span{overflow-wrap:break-word;font-size:2rem;color:#E56464}@media (max-width: 500px){.converter__resultbox{font-size:.6em}.converter span{font-size:1rem}.converter button{font-size:0.5em}}.converter__select{display:block;font-size:16px;font-family:sans-serif;font-weight:700;color:#444;line-height:1.3;padding:.6em 1.4em .5em .8em;min-width:100%;max-width:200px;box-sizing:border-box;margin:0;margin:10px 0;border:1px solid #aaa;box-shadow:0 1px 0 1px rgba(0,0,0,0.04);border-radius:.5em;-moz-appearance:none;-webkit-appearance:none;appearance:none;scroll-behavior:initial;outline:none;background-image:url("data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E"),linear-gradient(to bottom, #fff 0%, #e5e5e5 100%);background-repeat:no-repeat, repeat;background-position:right .7em top 50%, 0 0;background-size:.65em auto, 100%}

.List{display:grid;grid-template-columns:repeat(5, 1fr);list-style:none;color:#fff;margin:50px 0 0;padding:0 5% 5%}.List>li{display:block;padding:1rem;margin:.8rem;box-shadow:0px 10px 10px 5px #0F1423;border-radius:8px;cursor:pointer}.List>h4{display:block;margin:0}.List__favorite{background-color:#E56464}@media (max-width: 900px){.List{grid-template-columns:repeat(3, 1fr)}}@media (max-width: 600px){.List{grid-template-columns:repeat(2, 1fr)}}@media (max-width: 400px){.List{grid-template-columns:1fr}}

